	export default {
		data() {
			return {
				article: {
					aId: 0,
					title: '',
					content: '',
					nickname: '',
					createTime: ''
				},
				articles: [],
				recommend: true,
				special: false,
				serialize: false,
				show: false,

			};
		},

		onLoad: function() {
			this.getArticles();
		},
		onShow: function() {},
		onPullDownRefresh: function() {
			this.getArticles();
		},
			components :{
			graceMaskView
		},
		methods: {
			// 第1个演示 开启与关闭
			showBanner: function() {
				this.show = true;
			},
			closeBanner: function() {
				this.show = false;
			},


			clickshow: function() {
				this.recommend = true;
				this.special = false;
				this.serialize = false;
			},
			clickshow2: function() {
				this.recommend = false;
				this.special = true;
				this.serialize = false;
			},
			clickshow3: function() {
				this.recommend = false;
				this.special = false;
				this.serialize = true;
			},

			getArticles: function() {
				var _this = this;
				uni.request({
					url: this.apiServer + '/article/list',
					method: 'GET',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: res => {
						_this.articles = res.data.data;
						for (var i = 0; i < _this.articles.length; i++) {
							_this.articles[i].createTime = this.handleTime(_this.articles[i].createTime);
							_this.articles[i].content = this.handleContent(_this.articles[i].content);
						}
					},
					complete: function() {
						uni.stopPullDownRefresh();
					}
				});
			},

			gotoDetail: function(aId) {
				uni.navigateTo({
					url: '../article_detail/article_detail?aId=' + aId
				})
			},

			handleTime: function(date) {
				var d = new Date(date);
				var year = d.getFullYear();
				var month = d.getMonth() + 1;
				var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate();
				var hour = d.getHours() < 10 ? '0' + d.getHours() : '' + d.getHours();
				var minutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : '' + d.getMinutes();
				var seconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : '' + d.getSeconds();
				return year + '-' + month + '-' + day + ' ' + hour + ":" + minutes + ':' + seconds
			},

			handleContent: function(msg) {
				let description = msg;
				description = description.replace(/(\n)/g, "");
				description = description.replace(/(\t)/g, "");
				description = description.replace(/(\r)/g, "");
				description = description.replace(/<\/?[^>]*>/g, "");
				description = description.replace(/\s*/g, "");
				return msg.substring(0, 50);;
			},
			//         window.onload=function(){
			// 			var oBth=document.getElementById
			// 		}
		}
	}
